* {
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: "Nunito Sans", sans-serif;
  overflow-x: hidden;
}
.rating {
  margin-bottom: 5px;
  .stars {
    i {
      color: rgb(255, 153, 0);
    }
  }
}
.app {
  height: auto;
}
.dark-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.507);
}
.section {
  width: 100vw;
  position: relative;
  background-color: rgb(208, 229, 255);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
  padding: 20px;
  margin-bottom: 40px;
  overflow: hidden;
  .arrow {
    position: absolute;
    top: 50%;
    translate: 0 -50%;
    width: 50px;
    aspect-ratio: 1/1;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.274);
    font-size: 30px;
    color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    transition: 0.3s;
    z-index: 2;
    cursor: pointer;
    &:hover {
      scale: 1.1;
    }
    &::selection {
      background-color: transparent;
    }
  }
  .left-arrow {
    left: 50px;
  }
  .right-arrow {
    right: 50px;
  }
  .section-title {
    padding-left: 20px;
    font-weight: 600;
    font-size: 30px;
  }
  .products-line {
    width: 100vw;
    padding: 30px;
    display: flex;
    align-items: center;
    gap: 20px;
    transition: 0.7s;
    .product {
      width: 300px;
      min-width: 288px;
      height: 450px;
      border: solid #fff 2px;
      display: flex;
      flex-direction: column;
      border-radius: 10px;
      overflow: hidden;
      box-shadow: 0 0 15px rgba(0, 0, 0, 0.237);
      .top {
        width: 100%;
        height: 50%;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #fff;
        img {
          width: 70%;
          transition: 0.6s;
          &:hover {
            scale: 1.1;
          }
        }
      }
      .bottom {
        position: relative;
        background-color: #fff;
        height: 50%;
        padding: 20px;
        .title-view {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: -5px;
          .title {
            text-align: start;
            font-size: 20px;
            overflow: hidden;
            width: 200px;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
          }
          a {
            background-color: rgb(0, 110, 255);
            padding: 5px 15px;
            border: 2px solid #fff;
            border-radius: 5px;
            font-weight: 600;
            cursor: pointer;
            transition: 0.3s;
            text-decoration: none;
            color: #fff;
            &:hover {
              background-color: #fff;
              border: 2px solid rgb(0, 110, 255);
              color: rgb(0, 110, 255);
            }
          }
        }
        .description {
          margin: 0;
          margin-bottom: 70px;
          font-size: 15px;
          opacity: 0.8;
          line-height: 18px;
          overflow: hidden;
          width: 100%;
          display: -webkit-box;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;
        }
        .price-count {
          width: 100%;
          position: absolute;
          bottom: 0;
          left: 50%;
          translate: -50%;
          display: flex;
          justify-content: space-evenly;
          .price {
            font-weight: 600;
            color: rgb(0, 88, 202);
          }
        }
      }
    }
  }
}
.count {
  font-weight: 600;
  &.high {
    color: green;
  }
  &.low {
    color: rgb(255, 86, 86);
  }
}
@media (max-width: 480px) {
  .App {
    .navbar {
      padding: 10px;
      .left {
        a {
          margin-left: 5px;
          margin-right: 5px;
          h1 {
            font-size: 25px;
            margin-left: 0;
          }
        }
      }
      .center {
        input {
          height: 35px;
        }
        a {
          width: 50px;
          button {
            width: 100%;
            height: 35px;
          }
        }
      }
      .right {
        .user-image {
          width: 30px;
        }
      }
      .icon {
        width: 30px;
        height: 30px;
        &:hover {
          border: 2px solid transparent;
        }
        i {
          font-size: 20px;
        }
        p {
          top: -20px;
          right: -10px;
        }
      }
    }
    .section {
      padding: 0;
      .products-line {
        display: flex;
        flex-direction: column;
        height: 1400px;
        .product {
          width: 80vw;
          margin: 10px 0;
          min-height: 400px;
        }
      }
      .arrow {
        display: none;
      }
      .section-title {
        padding-left: 0;
        margin-top: 20px;
        text-align: center;
      }
      .show-more {
        position: absolute;
        bottom: 0;
        width: 100%;
        z-index: 1;
        height: 100px;
        background: linear-gradient(
          to top,
          #fff,
          #fff,
          rgba(255, 255, 255, 0.151)
        );
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 800;
        font-size: 20px;
        color: rgba(0, 0, 0, 0.733);
        cursor: pointer;
      }
    }
    .home-container {
      .home-first-bar {
        flex-direction: column;
        .left {
          h1 {
            font-size: 30px;
            width: 100%;
            height: 50%;
            margin-top: 60px;
          }
        }
        .bundle-image {
          width: 100%;
          height: 50%;
          overflow: visible;
          padding-top: 50px;
          box-sizing: content-box;
          img {
            height: 130%;
          }
        }
      }
    }
    .product-page {
      .product-details {
        margin-top: 80px !important;
        flex-direction: column;
        width: 90% !important;
        .left {
          width: 100% !important;
        }
        .right {
          width: 100% !important;
          margin-left: 0 !important;
          .title {
            width: 100% !important;
          }
          .description {
            width: 100% !important;
          }
        }
        .buttons {
          position: static !important;
        }
      }
    }

    .profile-page {
      flex-direction: column;
      height: auto;
      .left-side {
        margin-top: 30px;
        width: 100%;
      }
      .center-side {
        width: 100%;
        .favourite-list {
          .favourite-item {
            padding: 10px 20px;
            .left {
              .image {
                width: 60px;
                height: 60px;
                margin-right: 10px;
              }
            }
            .center {
              a {
                .title {
                  font-size: 17px;
                }
              }
              .rating {
                .stars {
                  font-size: 13px;
                }
              }
              .price-count {
                flex-direction: column;
                .price,
                .count {
                  font-size: 13px;
                }
              }
            }
            .right {
              button {
                font-size: 11px;
                padding: 10px 12px;
                font-weight: 600;
              }
            }
          }
        }
      }
      .right-side {
        width: 100%;
        .orders-list {
          .order-item {
            margin-top: 5px;
            padding: 20px 20px;
            .left {
              .image {
                width: 60px;
                height: 60px;
                margin-right: 10px;
              }
            }
            .center {
              a {
                .title {
                  font-size: 17px;
                }
              }
              .rating {
                .stars {
                  font-size: 13px;
                }
              }
              .price-count {
                width: 100%;
                .price,
                .count {
                  font-size: 13px;
                }
              }
            }
            .right {
              height: 100%;
              gap: 5px;
              button {
                font-size: 11px;
                width: 100px;
                padding: 10px 6px;
                font-weight: 600;
              }
            }
          }
        }
      }
      .right-side {
        width: 100%;
        margin-bottom: 30px;
      }
    }
  }
  .navbar {
    .check-out {
      height: 600px;
      width: 90vw;
      .products {
        padding: 20px;
        .product {
          align-items: center;
          .left {
            width: 60px;
            height: 60px;
            margin-right: 10px;
          }
          .right {
            a {
              .title {
                font-size: 14px;
                margin-bottom: 3px;
              }
            }
            .price {
              margin: 2px;
              font-size: 12px;
            }
            .check-out-buttons {
              .remove {
                width: 65px;
              }
              .change-buttons {
                button {
                  width: 30px;
                }
              }
            }
          }
        }
      }
      .total {
        font-size: 18px;
      }
      .buttons {
        .next-step,
        a .next-step {
          font-size: 17px;
          padding: 5px 10px;
        }
      }
      .step2 {
        .step2-container {
          font-size: 12px;
          margin-left: 0;
          .checkout-user-detail {
            align-items: flex-start;
            height: 45px;
            h3 {
              margin: 0;
              font-size: 17px;
            }
            span {
              margin-top: 2px;
              width: 50%;
              font-size: 14px;
            }
          }
        }
      }
      .last-step {
        font-size: 13px;
      }
    }
  }
}

.check-out {
  position: absolute;

  left: 50%;
  translate: -50% -50%;
  width: 600px;
  height: 700px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.219);
  animation: check-out-move 1s;
}
.show-more {
  display: none;
}
